<template>
<div>

  <nav-bar></nav-bar>
  <section class="resturant-main tab-content header-top">
    <div class="container">
      <div class="row">
        <div class="col-md-12">
          <home-slider />
          <home-title title="All Restaurants Nearby" subtitle="Discover Unique Taste around you.">
            <filter-sort></filter-sort>
             <!-- <a href="javascript:void(0)" data-toggle="modal" data-target="#filter">Filter/Sort</a> -->
          </home-title>
          <div class="resturant-item-main" v-if="items && items.length > 0">
            <item v-for="item in items" :key="item.name" :item="item" :currencys="currencys"></item>
          </div>
          <div v-else class="order-offers rest-not-found">
            <div class="text-center my-2" v-if="loading">
              <b-spinner class="align-middle"></b-spinner>
            </div>
            <p v-else>No Restaurants Found</p>
          </div>
        </div>
      </div>
    </div>
  </section>
</div>
</template>

<script>
// @ is an alias to /src
import HomeSlider from "@/components/HomeSlider.vue";
import HomeTitle from "@/components/HomeTitle.vue";
import NavBar from "@/components/NavBar.vue";
import Item from "@/components/Item.vue";
import FilterSort from "@/components/FilterSort.vue";
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  name: "Home",
  components: {
    HomeSlider,
    HomeTitle,
    NavBar,
    Item,
    FilterSort
  },
  data() {
    return {
      items: [],
      banner: [],
      currencys: '',
      sort: '',
      cuisine_id: '',
      marker: { position: { lat: 10, lng: 10 } },
      loading: true,
      setting: {}
    };
  },
  computed: {
    ...mapGetters("restaurant",["restaurants","banners"]),
    ...mapGetters("setting",["settings"]),
    ...mapState({
        currency: (state) => state.setting.settings.currency,
      }),
  },
  mounted() {
    this.geolocate();
    this.getRestaurant();
    this.getBanner();
    this.getsetting();
    this.currencys = this.currency;
  },
  methods: {
    ...mapActions("restaurant",["getRestaurants","getBanners"]),
     ...mapActions("setting",["getsetting"]),
    geolocate() {
      navigator.geolocation.getCurrentPosition((position) => {
        this.marker.position = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
        this.getRestaurant(position.coords.latitude,position.coords.longitude);
        this.getBanner(position.coords.latitude,position.coords.longitude)
      });
    },
    getRestaurant(lat,lng){
      this.loading = true;
      this.getRestaurants({
          date: '',
          cityname: 'Ahmedabad',
          latitude: lat,
          cuisine_id: this.cuisine_id,
          skip: 0,
          starttime: '',
          is_langauge: 'en',
          sort_type: this.sort,
          category_id: '',
          user_id: '',
          filter_type: '',
          cost_range: '',
          vendor_id: window.location.pathname.split('/')[2],
          state: 'Gujarat',
          delivery_type_time_slots: '',
          longitude: lng
      }).then(data => {
          this.loading = false;
          this.items = data.Result;  
      })
    },
    getBanner(lat,lng){
      this.getBanners({
        vendor_id: window.location.pathname.split('/')[2],
        latitude: lat,
        longitude: lng
      }).then(data => {
            this.banner = data.Result;
      })
    }
  }
};
</script>
